import React, { FC, ImgHTMLAttributes } from "react";
import styled from "styled-components";

type ModalImageProps = ImgHTMLAttributes<HTMLImageElement>;

const ImageWrapper = styled.div`
	display: flex;
	justify-content: center;
	padding-top: 24px;
`;

const ModalImage: FC<ModalImageProps> = (props) => (
	<ImageWrapper>
		<img alt={props.alt} {...props} data-qa="modal-image" />
	</ImageWrapper>
);

ModalImage.displayName = "ModalImage";
export default ModalImage;
